import CarouselItem from "./CarouselItem";
import './style.css';
import Slider from 'react-slick';

export default function BannerCarousel() {
  const imgArray = Array(6).fill(0);

  return (
    <>
          {/* <img height={500} src={img1} /> */}

      <Slider className="banner-carousel" arrows dots>
        {imgArray.map( (_, i)=>{
            const src =  require(`../../assets/img/banners/${i+1}-min.webp`);
            console.log({src: src});
            return <div>
            <CarouselItem src={src} />
            </div>
        })}
        
      </Slider>
    </>
  );
}
