import { useState } from "react";
import "./styles.css";

function ContactUs() {
  const [contactData, setContactData] = useState({});

  async function handleFormSubmit() {
    if (
      !contactData.name ||
      !contactData.mobile ||
      !contactData.email ||
      !contactData.message
    ) {
      return alert("Please fill all information.");
    }
    if (contactData.mobile.length !== 10) {
      return alert("Please enter correct mobile number.");
    }

    const formLink = `https://docs.google.com/forms/d/e/1FAIpQLSc-aam0nEWO2wWFiyulDOw-73k_J6AOYSU1UeRtKs353aS0UQ/formResponse?usp=pp_url&entry.1586260119=${contactData.name}&entry.413557525=${contactData.mobile}&entry.308718422=${contactData.email}&entry.2015843977=${contactData.message}&submit=Submit`;

    try {
      await fetch(formLink);
    } catch (error) {
    } finally {
      alert(
        "Thank you for submitting your query. We will contact you in sometime."
      );
    }
  }

  function handleInputChange(e) {
    const { name, value } = e.target;
    setContactData((prevData) => ({ ...prevData, [name]: value }));
  }

  return (
    <>
      <div className="contact-us-container bg-primary-2">
        <div className="contact-us-first-container">
          {/* this section will show a image carousel of patients */}

          <p>Contact us</p>
          <p>send your query directly to us.</p>
          <div>
            <p>
              or you can{" "}
              <a href="https://calendly.com/abha_sharma/30min" target="_blank" rel="noreferrer">
                {" "}
                book online consultation
              </a>
            </p>
          </div>
        </div>
        <div>
          {/* this section will show a contat us form */}
          <div className="contact-form-container">
            <label>
              Name
              <br />
              <input
                type="text"
                placeholder="enter your name"
                name="name"
                onChange={handleInputChange}
              />
            </label>
            <br />
            <label>
              Mobile Number
              <br />
              <input
                type="number"
                placeholder="enter your mobile number"
                name="mobile"
                onChange={handleInputChange}
              />
            </label>
            <br />
            <label>
              Email
              <br />
              <input
                type="email"
                placeholder="enter your email address"
                name="email"
                onChange={handleInputChange}
              />
            </label>
            <br />
            <label>
              Query/Message
              <br />
              <textarea
                placeholder="enter your query"
                name="message"
                onChange={handleInputChange}
              />
            </label>
            <br />
            <button className="bg-primary" onClick={handleFormSubmit}>
              Submit
            </button>
          </div>
        </div>
      </div>
    </>
  );
}
export default ContactUs;
